import React from 'react';
import NavbarElement from '../NavbarElement/NavbarElement';
import classes from './NavbarMain.css';
const NavbarMain = (props) => (
    <div className={classes.NavbarMain}>
        <NavbarElement />
    </div>
);

export default NavbarMain;


